/*
Template Name: FlexAdmin - Tailwind CSS 3 Admin Layout & UI Kit Template
Version: 1.0
Author: MyraStudio
File: Main Css File
*/
@tailwind base;
@tailwind components;
@tailwind utilities;
html {
  position: relative;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  @apply antialiased font-base font-normal text-sm leading-normal bg-default-100 text-default-500;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @apply font-semibold text-gray-800;
}

code {
  @apply text-sky-500;
}

.text-muted {
  @apply text-gray-400;
}

.form-input,
input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent;
}

.icons-list-demo i {
  text-align: center;
  vertical-align: middle;
  font-size: 28px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  margin-right: 12px;
  border-radius: 3px;
  display: inline-block;
  transition: all 0.2s;
}
.icons-list-demo i + span {
  display: inline-flex;
  user-select: all;
}

.kanban-board {
  position: relative;
}
.kanban-board:before {
  content: "No Tasks";
  position: absolute;
  display: flex;
  inset-inline-start: 50%;
  top: 0;
  transform: translate(-50%);
  min-height: 100px;
  justify-content: center;
  line-height: 110px;
  z-index: -10;
  text-align: center;
  font-weight: 600;
}

.custom-scroll::-webkit-scrollbar {
  -webkit-appearance: none;
}
.custom-scroll::-webkit-scrollbar:vertical {
  width: 4px;
}
.custom-scroll::-webkit-scrollbar:horizontal {
  height: 4px;
}
.custom-scroll::-webkit-scrollbar-thumb {
  @apply rounded-lg bg-slate-300/50;
}
.custom-scroll::-webkit-scrollbar-track {
  @apply bg-transparent rounded-lg;
}
.custom-scroll::-webkit-scrollbar-corner {
  @apply bg-transparent;
}

.filter-options li a {
  @apply border-light text-slate-900;
}

.filter-options li:hover a,
.filter-options li.active a,
.filter-options li:active a,
.filter-options li:focus a {
  @apply bg-primary text-white border-transparent shadow-primary/50;
}

@layer components {
  .btn {
    @apply inline-flex justify-center items-center gap-x-2 text-center rounded-lg border border-transparent px-3 py-1.5 text-sm transition-all font-semibold disabled:opacity-50 disabled:pointer-events-none;
  }
  .btn.btn-lg {
    @apply px-5 py-3 text-base;
  }
  .btn.btn-sm {
    @apply px-3.5 py-1.5 text-xs;
  }
}
@layer components {
  .card {
    @apply relative bg-white shadow flex flex-col break-words bg-clip-border rounded-xl h-fit;
  }
  .card .card-header {
    @apply py-3 px-6 border-b border-gray-200;
  }
  .card .card-header.card-tabs {
    @apply pb-0;
  }
  .card .card-footer {
    @apply py-3 px-6 border-t border-gray-200;
  }
  .card .card-body {
    @apply p-5;
  }
  .card-title {
    @apply text-base font-semibold text-gray-800;
  }
}
@layer components {
  label {
    @apply inline-block text-sm font-semibold cursor-pointer text-gray-600;
  }
  .form-input,
  .form-textarea,
  .form-select {
    @apply font-normal rounded block w-full border-gray-200 text-sm focus:border-gray-300 
        focus:ring-0 bg-white;
  }
  .form-checkbox,
  .form-radio {
    @apply bg-inherit cursor-pointer border-gray-300 focus:ring-0 focus:ring-offset-0;
  }
  .form-switch {
    @apply w-9 h-5 flex items-center appearance-none bg-gray-200 border-2 border-transparent
        rounded-full focus:ring-0 focus:ring-offset-0 cursor-pointer transition-colors ease-in-out duration-200 checked:bg-none;
  }
  .form-switch::before {
    @apply content-[""] inline-block w-4 h-4 bg-white rounded-full translate-x-0 transform transition ease-in-out checked:translate-x-full duration-200;
  }
  .form-switch.form-switch-lg {
    @apply w-11 h-6;
  }
  .form-switch.form-switch-lg::before {
    @apply w-5 h-5;
  }
  .card-radio {
    @apply relative;
  }
  .card-radio .form-label {
    @apply border overflow-hidden text-ellipsis whitespace-nowrap flex flex-col cursor-pointer border-gray-200;
  }
  .card-radio .form-radio {
    @apply hidden;
  }
  .card-radio .form-radio:checked + .form-label {
    @apply border-primary text-primary;
  }
  .btn-radio .form-radio {
    @apply hidden;
  }
  .btn-radio .form-radio:checked + label {
    @apply bg-primary text-white;
  }
}
.morris-chart text {
  font-family: "Work Sans", sans-serif !important;
}

.morris-hover {
  position: absolute;
  z-index: 10;
}
.morris-hover.morris-default-style {
  font-size: 12px;
  text-align: center;
  border-radius: 5px;
  padding: 10px 12px;
  font-family: "Work Sans", sans-serif !important;
  @apply bg-dark text-white;
}
.morris-hover.morris-default-style .morris-hover-row-label {
  font-weight: bold;
  margin: 0.25em 0;
  font-family: "Work Sans", sans-serif !important;
}
.morris-hover.morris-default-style .morris-hover-point {
  white-space: nowrap;
  margin: 0.1em 0;
  color: white;
}

.page-content {
  @apply min-h-screen flex flex-col lg:ps-64 w-full transition-all duration-300;
}

html[data-sidebar-view=hidden] #app-menu {
  @apply -ms-64;
}
html[data-sidebar-view=hidden] .page-content {
  @apply lg:ps-0;
}

.admin-menu a.active {
  @apply text-primary bg-primary/10;
}
.admin-menu a.active .material-symbols-rounded {
  @apply fill-1;
}
.admin-menu .hs-accordion.active > .hs-accordion-toggle.hs-accordion-active\:bg-default-100 {
  @apply text-primary bg-primary/10;
}
.admin-menu .hs-accordion-content .active {
  @apply text-primary bg-transparent;
}

[data-simplebar] {
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
}

.simplebar-wrapper {
  overflow: hidden;
  width: inherit;
  height: inherit;
  max-width: inherit;
  max-height: inherit;
}

.simplebar-mask {
  direction: inherit;
  position: absolute;
  overflow: hidden;
  padding: 0;
  margin: 0;
  inset-inline-start: 0;
  top: 0;
  bottom: 0;
  inset-inline-end: 0;
  width: auto !important;
  height: auto !important;
  z-index: 0;
}

.simplebar-offset {
  direction: inherit !important;
  box-sizing: inherit !important;
  resize: none !important;
  position: absolute;
  top: 0;
  inset-inline-start: 0 !important;
  bottom: 0;
  inset-inline-end: 0 !important;
  padding: 0;
  margin: 0;
  -webkit-overflow-scrolling: touch;
}

.simplebar-content-wrapper {
  direction: inherit;
  box-sizing: border-box !important;
  position: relative;
  display: block;
  height: 100%;
  /* Required for horizontal native scrollbar to not appear if parent is taller than natural height */
  width: auto;
  visibility: visible;
  overflow: auto;
  /* Scroll on this element otherwise element can't have a padding applied properly */
  max-width: 100%;
  /* Not required for horizontal scroll to trigger */
  max-height: 100%;
  /* Needed for vertical scroll to trigger */
  scrollbar-width: none;
  padding: 0px !important;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
  display: none;
}

.simplebar-content:before,
.simplebar-content:after {
  content: " ";
  display: table;
}

.simplebar-placeholder {
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  pointer-events: none;
}

.simplebar-height-auto-observer-wrapper {
  box-sizing: inherit !important;
  height: 100%;
  width: 100%;
  max-width: 1px;
  position: relative;
  float: inset-inline-start;
  max-height: 1px;
  overflow: hidden;
  z-index: -1;
  padding: 0;
  margin: 0;
  pointer-events: none;
  flex-grow: inherit;
  flex-shrink: 0;
  flex-basis: 0;
}

.simplebar-height-auto-observer {
  box-sizing: inherit;
  display: block;
  opacity: 0;
  position: absolute;
  top: 0;
  inset-inline-start: 0;
  height: 1000%;
  width: 1000%;
  min-height: 1px;
  min-width: 1px;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
}

.simplebar-track {
  z-index: 1;
  position: absolute;
  inset-inline-end: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden;
}

[data-simplebar].simplebar-dragging .simplebar-content {
  pointer-events: none;
  user-select: none;
  -webkit-user-select: none;
}

[data-simplebar].simplebar-dragging .simplebar-track {
  pointer-events: all;
}

.simplebar-scrollbar {
  position: absolute;
  inset-inline-end: 2px;
  width: 4px;
  min-height: 10px;
}

.simplebar-scrollbar:before {
  content: "";
  position: absolute;
  border-radius: 7px;
  inset-inline-start: 0;
  inset-inline-end: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
  @apply bg-slate-300;
}

.simplebar-scrollbar.simplebar-visible:before {
  /* When hovered, remove all transitions from drag handle */
  opacity: 0.5;
  transition: opacity 0s linear;
}

.simplebar-track.simplebar-vertical {
  top: 0;
  width: 11px;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
  top: 2px;
  bottom: 2px;
}

.simplebar-track.simplebar-horizontal {
  inset-inline-start: 0;
  height: 11px;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar:before {
  height: 100%;
  inset-inline-start: 2px;
  inset-inline-end: 2px;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  inset-inline-end: auto;
  inset-inline-start: 0;
  top: 2px;
  height: 7px;
  min-height: 0;
  min-width: 10px;
  width: auto;
}

/* Rtl support */
[data-simplebar-direction=rtl] .simplebar-track.simplebar-vertical {
  inset-inline-end: auto;
  inset-inline-start: 0;
}

.simplebar-dummy-scrollbar-size {
  direction: rtl;
  position: fixed;
  opacity: 0;
  visibility: hidden;
  height: 500px;
  width: 500px;
  overflow-y: hidden;
  overflow-x: scroll;
}

.simplebar-hide-scrollbar {
  position: fixed;
  inset-inline-start: 0;
  visibility: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
}

[data-simplebar-lg] .simplebar-scrollbar {
  inset-inline-end: 1px;
  width: 10px;
}

[data-simplebar-primary] .simplebar-scrollbar:before {
  @apply bg-primary;
}